.welcomeMenu {
  position: absolute;
  height: 500px;
  width: 500px;
  background-color: #b17e24;
  border-radius: 50%;
  margin: auto;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  opacity: 0.9;
  box-shadow: 0px 2px 29px 12px rgba(86, 86, 86, 0.99);
  -webkit-box-shadow: 0px 2px 29px 12px rgba(109, 109, 109, 0.99);
  -moz-box-shadow: 0px 2px 29px 12px rgba(86, 86, 86, 0.99);
  opacity: 0;
}

.logo {
  position: relative;
  top: 0px;
  opacity: 0;
}

.suggest-button {
  display: -webkit-box;
  margin: auto;
  background-color: white;
  border: 1px solid white;
  position: relative;
  top: 200px;
  height: 12%;
  width: 48%;
  border-radius: 29px;
  font-size: 25px;
  padding-top: 6px;
  color: #bb8008;
  font-weight: 700;
  opacity: 0;
  box-shadow: 0px 3px 62px 7px rgba(148,148,148,1);
  cursor: pointer;
}

.suggest-button:hover {
  box-shadow: 0px 3px 62px 17px rgba(148,148,148,1);
}

.store-icon {
  height: 26px;
}

.play-store-icon {
  margin-right: 12px;
}

.app-store-icon {
  margin-left: 12px;
}

.store-icon-div {
  background-color: #4a290069;
  display: -webkit-inline-box;
  position: relative;
  margin-top: 68px;
  opacity: 0.8;
  height: 38px;
  width: 40px;
  border-radius: 50%;
  padding-top: 12px;
  padding-left: 13px;
  cursor: pointer;
}

.store-icon-div:hover {
  opacity: 1;
}

.rights {
  margin-top: 22px;
  font-size: 13px;
  color: white;
}

.extra-info {
  opacity: 0;
}

@media only screen and (max-width: 768px) {
  .welcomeMenu {
    height: 300px;
    width: 300px;
  }  
  .suggest-button {
    font-size: 19px;
    height: 15%;
  }
  .rights {
    position: fixed;
    bottom: 10px;
    width: 170px;
    /* margin-top: 51px; */
    left: calc(50% - 85px);
  }
  .store-icon-div {
    background-color: #4a290069;
    display: -webkit-inline-box;
    position: relative;
    margin-top: 43px;
    opacity: 0.8;
    height: 29px;
    width: 31px;
    border-radius: 50%;
    padding-top: 12px;
    padding-left: 13px;
    cursor: pointer;
  }
  
  .store-icon {
    height: 18px;
  }
  
}
.App {
  text-align: center;
}

button {
  font-family: 'El Messiri', sans-serif;
}

p {
  margin: 0px;
  padding: 0px;
}

body {
  background: url('https://wadhahessam.github.io/restaurant-suggestion/img/background3.jpg') no-repeat center center fixed;
  background-size: cover;
  margin: 0;
  padding: 0;
  font-family: 'El Messiri', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #5d5d5d;
}


